<template>
  <div id="app" ref="app">
    <router-view />
  </div>
</template>

<script>
import { webS } from './common/js/websocket'

export default {
  name: 'App',
  mounted () {
    this.init()
    this.login()
  },
  destroyed () {
    window.removeEventListener('unload', this.saveState)// 解决刷新后vux内容丢失问题
  },
  methods: {
    init () {
      document.documentElement.style.fontSize = document.documentElement.clientWidth / 25 + 'px'
      window.addEventListener('unload', this.saveState)// 解决刷新后vux内容丢失问题
    },
    saveState () {
      this.clearState()
      sessionStorage.setItem('state', JSON.stringify(this.$store.state))
      sessionStorage.setItem('state_page', 1)
    },
    clearState () {
      this.$store.commit('setToContent', [])
      this.$store.commit('setMessageTime', 0)
    },
    // 获取客户登录的信息
    login () {
      // 先从客户端获取客户登录信息
      let login = true
      // 获取成功
      if (login) {
        localStorage.invest_h5_token = '1111111'
        localStorage.useid = '123123123'
        // 建立websocket连接
        webS(this)
      } else {
        this.$Dialog.Dialog({
          text: '对不起，获取登录信息失败',
          type: 'quit'
        })
      }
    }
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /*text-align: center;*/
    color: #333333;
    margin-top: 60px;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .header_left {
    border-width: 0.15rem 0.15rem 0 0;
    border-color: white;
    float: left;
    width: 0.8rem;
    height: 0.8rem;
    display: inline-block;
    border-style: solid;
    margin-top: 1rem;
    margin-left: 2rem;
    -webkit-transform: matrix(-0.71, 0.71, 0.71, 0.71, 0, 0);
  }

  html {
    /*font-size: 10px;*/
  }

  * {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
</style>



