import Vue from 'vue'
import store from '../store'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// 跳转到当前页面时报错
const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return routerPush.call(this, location).catch(error => error)
}
const routes = [
  {
    path: '/',
    // name: 'homeView',
    component: () => import('../views/homeView.vue'),
    children: [
      {
        path: '/',
        // name: 'home',
        component: () => import('../views/home/home.vue'),
        children: [
          {
            path: '/',
            name: 'DiscussionGroups',
            component: () => import('../views/home/views/DiscussionGroups/DiscussionGroups.vue')
          },
          {
            path: '/information',
            name: 'information',
            component: () => import('../views/home/views/information/information.vue')
          },
          {
            path: '/stocks',
            name: 'stocks',
            component: () => import('../views/home/views/stocks/stocks.vue')
          },
          {
            path: '/consulting',
            name: 'consulting',
            component: () => import('../views/home/views/consulting/consulting.vue')
          },
          {
            path: '/mine',
            name: 'mine',
            component: () => import('../views/home/views/mine/mine.vue'),
            children: [
              {
                path: '/PA',
                name: 'PA',
                component: () => import('../views/home/views/mine/pages/PA.vue')
              }, {
                path: '/Collect',
                name: 'Collect',
                component: () => import('../views/home/views/mine/pages/Collect.vue')
              }, {
                path: '/Attended',
                name: 'Attended',
                component: () => import('../views/home/views/mine/pages/Attended.vue')
              }
            ]
          },
          {
            path: '/interest',
            name: 'interest',
            component: () => import('../views/home/views/interest/interest.vue')
          }
        ]
      },
      {
        path: '/ccscResearch',
        // name: 'ccscResearch',
        component: () => import('../views/home/ccscResearch.vue'),
        children: [
          {
            path: '/',
            name: 'newP',
            component: () => import('../views/home/ccscResearch/newP.vue')
          }, {
            path: '/newS',
            name: 'newS',
            component: () => import('../views/home/ccscResearch/newS.vue')
          }
        ]
      }, {
        path: '/ccscClassroom',
        // name: 'ccscResearch',
        component: () => import('../views/home/ccscClassroom.vue'),
        children: [
          {
            path: '/',
            name: 'videoP',
            component: () => import('../views/home/ccscClassroom/videoP.vue')
          }, {
            path: 'videoS',
            name: 'videoS',
            component: () => import('../views/home/ccscClassroom/videoS.vue')
          }
        ]
      },
      {
        path: '/advisory',
        name: 'advisory',
        component: () => import('../views/home/advisory.vue'),
        children: [
          {
            path: '/questions',
            name: 'questions',
            component: () => import('../views/home/advisory/qusetions.vue')
          }, {
            path: '/answer',
            name: 'answer',
            component: () => import('../views/home/advisory/answer.vue')
          }, {
            path: '/onlineService',
            name: 'onlineService',
            component: () => import('../views/home/advisory/onlineService.vue')
          }, {
            path: '/consult',
            name: 'consult',
            component: () => import('../views/home/advisory/consult.vue')
          }, {
            path: '/evaluate',
            name: 'evaluate',
            component: () => import('../views/home/advisory/evaluate.vue')
          }, {
            path: '/EvaluationSuccess',
            name: 'EvaluationSuccess',
            component: () => import('../views/home/advisory/EvaluationSuccess.vue')
          }
        ]
      }
    ]
  }
]
const configRouter = {
  routes: routes
}
const Router = new VueRouter(configRouter)
Router.beforeEach((to, from, next) => {
  console.log(to.name)
  if (store.state.OldPage === to.name) {
    // 返回时数据初始化，mounted不会执行问题
    store.commit('setToContent', [])
  }
  if (from.name === '/' && store.state.CurrentPage !== '/') {
    next({
      name: store.state.CurrentPage
    })
  }
  // 记录跳转页面
  store.commit('setCurrentPage', to.name)
  store.commit('setOldPage', from.name)
  // 更换页面时聊天记录中显示时间的计算方法所要求显示第一个时间
  store.commit('setMessageTime', 0)
  next()
})
export default Router



