import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: sessionStorage.getItem('state') ? JSON.parse(sessionStorage.getItem('state')) : {
    MessageTime: 0,
    CurrentPage: '/',
    OldPage: '/',
    CurrentTitle: '',
    CurrentArticle: '',
    CommentSubmit: '',
    SendMessage: '',
    ToContent: []
  },
  mutations: {
    setMessageTime (state, time) {
      state.MessageTime = time
    },
    setCurrentPage (state, page) {
      state.CurrentPage = page
    },
    setOldPage (state, page) {
      state.OldPage = page
    },
    setCurrentTitle (state, name) {
      state.CurrentTitle = name
    },
    setCurrentArticle (state, vale) {
      state.CurrentArticle = vale
    },
    setCommentSubmit (state, vale) {
      state.CommentSubmit = vale
    },
    setSendMessage (state, vale) {
      state.SendMessage += vale
    },
    setToContent (state, vale) {
      state.ToContent = vale
    }
  }
})
export default store



