import protobuf from 'protobufjs'

let that

const creatWebsocket = function () {
  if (window.ws && window.ws.readyState === 1) {

  } else {
    window.ws = new WebSocket(window.envConf.wsAddress)
  }
}
window.onbeforeunload = function () {
  window.ws.close()
}
const webS = function (the) {
  that = the
  creatWebsocket()
  initWebsocket()
}
const initWebsocket = function () {
  window.ws.onopen = onOpen
  window.ws.onclose = onClose
  window.ws.onmessage = onMessage
  window.ws.onerror = onError
}
const onOpen = function (evt) {
  console.log('开始建立')
}
const onClose = function (evt) {
  console.log('开始关闭')
}
const onMessage = function (evt) {
  console.log('收到消息')
  protobufToScren(evt.data, (acceptedData) => {
    if (acceptedData.module === 10000 && acceptedData.cmd === 101) {
    } else {
      that.$store.commit('setSendMessage', acceptedData)
    }
  })
}
const onError = function (evt) {
  console.log('错误')
}
const protobufToPost = function (data, fun) {
  if (window.WSMessage) {
    let wsmessage = window.WSMessage.create(data) // 需要序列化的数据 wsmessage为proto实例
    let buffer = window.WSMessage.encode(wsmessage).finish()
    fun(buffer)
  } else {
    loadProtoBuf(() => {
      let wsmessage = window.WSMessage.create(data) // 需要序列化的数据 wsmessage为proto实例
      let buffer = window.WSMessage.encode(wsmessage).finish()
      fun(buffer)
    })
  }
}
/**
 * 加载protobuf文件
 */
const loadProtoBuf = function (fn) {
  /* eslint-disable */
  protobuf.load('static/js/MessageProtocol.proto', function (err, root) {
    if (err) throw err
    // 把加载出来的文件暴露给全局
    window.WSMessage = root.lookup('wenlipackage.MessageProtocol') // 解码编码器
    fn()
  })
}
/**
 * 接收数据反序列化
 * @param data
 * @param fun
 */
const protobufToScren = function (data, fun) {
  if (window.WSMessage) {
    let reader = new FileReader()
    reader.readAsArrayBuffer(data)
    reader.onload = function (e) {
      let buf = new Uint8Array(reader.result)
      let buffer = window.WSMessage.decode(buf)
      fun(buffer)
    }
  } else {
    loadProtoBuf(() => {
      let reader = new FileReader()
      reader.readAsArrayBuffer(data)
      reader.onload = function (e) {
        let buf = new Uint8Array(reader.result)
        let buffer = window.WSMessage.decode(buf)
        fun(buffer)
      }
    })
  }
}
export {
  webS,
  protobufToPost
}



