<template>
  <div id="prompt">
    <p v-if="topShow" class="top">{{top_text}}</p>
    <p v-if="textShow" class="text">{{text}}</p>
    <img v-if="textShow" @click="close" src="@/common/images/关闭.png">
  </div>
</template>

<script>
export default {
  name: 'prompt',
  props: {
    top_text: {
      type: String,
      default: '置顶消息的优先级高于@消息提示，如果同时存在时，@提示位于其下方。'
    },
    topShow: {
      type: Boolean,
      default: false
    },
    textShow: {
      type: Boolean,
      default: false
    },
    text: {
      type: String,
      default: '您有被@的消息，点击下方筛选按键进行消息筛选查看'
    },
    ele: {
      type: HTMLBodyElement,
      default: () => document.body
    }
  },
  methods: {
    close () {
      if (this.topShow) {
        this.textShow = false
      } else {
        let vm = document.getElementById('prompt')
        this.ele.removeChild(vm)
      }
    }
  }
}
</script>

<style scoped>
  #prompt {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #FFFFFF;
    color: #333333;
    font-size: 0.85rem;
    font-weight: 500;
    z-index: 1;
  }

  #prompt .top {
    padding: 0.3rem 1rem;
  }

  #prompt .text {
    width: 100%;
    background-color: #FFF3F3;
    color: #333333;
    font-size: 0.85rem;
    margin: 0 auto;
    padding: 0.3rem 1rem;
    height: 1.8rem;
    line-height: 1.8rem;
  }

  #prompt img {
    position: absolute;
    right: 1rem;
    /* border: 1px dashed #666666; */
    bottom: 0.5rem;
  }
</style>



